	module.exports = {"data":{"post":{"html":"<p><i>For quite some time Christopher's favorite seemed to be \" Da Da Da\".  I couldn't figure out what he meant by saying that. He must have noticed because he switched to saying \"Mum Mum Mum\". Usually he says that right before, during or after his  meals. Maybe he's learned that his Mom has a lot do with his meals.</i></p>\n<p><i>Christopher is also starting to enjoy his weekly  bath.</i></p>\n<p>\"bath\"</p>\n<p><i>He splashes so much water around that everyone gets wet!</i></p>","fields":{"title":"First Words","date":"19 Aug 2000","url":"https://papascott.de/2000-08-19-first-words/"}}},"pathContext":{"slug":"/2000-08-19-first-words/","prev":{"fields":{"title":"Full coverage","date":"18 Aug 2000","slug":"/2000-08-18-full-coverage/"}},"next":{"fields":{"title":"Gateway Poster Child","date":"23 Aug 2000","slug":"/2000-08-23-gateway-poster-child/"}}}}

